.card {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 200px;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;
  transition: $transition;
}

.card__img {
  margin-bottom: 40px;
}

.card__product-name {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-align: left;
  color: $main-text-color;
  margin-bottom: 4px;
}

.card__product-code {
  font-size: 10px;
  line-height: 14px;
  text-align: left;
  color: $second-text-color;
  margin-bottom: 16px;
}

.card__raiting {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 24px;
}

.card__review {
  font-size: 10px;
  line-height: 14px;
  text-align: right;
  color: $main-text-color;
}

.card__price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.card__price-word {
  font-size: 12px;
  line-height: 18px;
  text-align: left;
  color: $second-text-color;
}

.card__price-num {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  text-align: right;
  color: $main-text-color;
}

.card__link {
  width: 166px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid $button-color;
  background-color: $button-color;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  padding-top: 12px;
}

.card__link:hover {
  background-color: #fff;
  color: $button-color;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.main {
  --column-count: 1;

  display: grid;
  padding: 50px 40px;
  gap: 46px 48px;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
}

@media (min-width: 488px) {
  .main {
    --column-count: 2;
  }
}

@media (min-width: 768px) {
  .main {
    --column-count: 3;
  }
}

@media (min-width: 1024px) {
  .main {
    --column-count: 4;
  }
}
